
.pretty-code-viewer pre code:before{content:none
}
pre code[class*="language-"],pre[class*="language-"]{white-space:pre-wrap;word-break:break-all !important
}
.pretty-code-viewer ul{margin-left:15px
}
.pretty-code-viewer ul ul{margin-left:30px
}
.pretty-code-viewer ul ul ul{margin-left:45px
}
.pretty-code-viewer ul ul ul ul{margin-left:60px
}
.pretty-code-viewer ul ul ul ul ul{margin-left:75px
}
.pretty-code-viewer>ul>li{margin-bottom:20px
}
.pretty-code-viewer.xml{white-space:pre-wrap;background:#f7f8f9;word-break:normal;padding:10px;color:#9aab3a;border-radius:5px;font-size:12px;line-height:20px
}
.pretty-code-viewer .token.punctuation{color:#394359
}
.pretty-code-viewer .token.tag{color:#394359
}
.pretty-code-viewer .token.attr-name{color:#8e0000
}
.pretty-code-viewer .token.attr-value{color:#2196f3
}
.pretty-code-viewer pre code{-webkit-box-shadow:none;box-shadow:none
}
.pretty-code-viewer .json-tree-value.json-tree-value-number{color:#2196f3
}
.pretty-code-viewer .json-tree-value.json-tree-value-boolean{color:#8e0000
}
